/*-------------------Modal-Button Instructions------------------------*/
.configuration .btn-instructions{
  transform:scale(1.5);
  cursor: pointer;
  animation: pulse-infinity 2s linear infinite;
  transition: all 2s ease-in-out;
}

.configuration .modal-instructions {
  right: 50%;
  width: 35vmax;
  height: 44vmax;
  overflow: hidden;
  display: grid;
  grid-template-rows: 5% auto 7%;
}

.modal-instructions .title-instructions{
  background: radial-gradient(circle, rgba(11, 5, 26, 0.815),rgba(25, 7, 66, 0.815), rgba(35, 10, 94, 0.829), rgba(25, 7, 66, 0.856), rgba(11, 5, 26, 0.849));
}

.modal-instructions .title-instructions span{
  animation: pulse-infinity 2s linear infinite;
  transition: all 2s ease-in-out;
}

.configuration .modal-instructions .instructions-content{
  display: flex;
  justify-content: space-between;
  height: 93.6%;
  width: 100%;
}

.modal-instructions .container-sections{
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 0.1vmax;
  background: linear-gradient(rgba(72, 35, 165, 0.842), rgba(85, 26, 224, 0.897), rgba(92, 28, 241, 0.842), rgba(85, 26, 224, 0.897), rgba(72, 35, 165, 0.842));
}

.container-sections button{
  border: none;
  height: 100%;
  font-size: 1.5vmax;
  background: linear-gradient(rgba(11, 5, 26, 0.815),rgba(25, 7, 66, 0.815), rgba(35, 10, 94, 0.829), rgba(25, 7, 66, 0.856), rgba(11, 5, 26, 0.849));
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5vmax;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.container-sections button:hover{
  background: linear-gradient(rgba(36, 17, 85, 0.842), rgba(55, 16, 145, 0.897), rgba(65, 19, 173, 0.842), rgba(55, 16, 145, 0.829), rgba(36, 16, 85, 0.856));
}

.container-sections button:active{
  background: linear-gradient(rgba(72, 35, 165, 0.842), rgba(85, 26, 224, 0.897), rgba(92, 28, 241, 0.842), rgba(85, 26, 224, 0.897), rgba(72, 35, 165, 0.842));
  transform: scale(1.1);
}


.section-instructions{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(36, 13, 56);
  padding: 0.7vmax;
  font-size: 1.4vmax;
}

.section-instructions h3{
  margin: 0.5vmax;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  -webkit-text-stroke: 0.03vmax rgb(162, 149, 187);
  text-transform: uppercase;
}

.section-instructions p{
  text-align: center;
  margin-bottom: 0.5vmax;
  text-shadow: 2px 2px 4px rgba(231, 255, 12, 0.5);
  background: rgba(255, 255, 0, 0.26);
  border-radius: 0.4vmax;
  box-shadow: 2px 2px 4px rgba(231, 255, 12, 0.575);
  
}

.section-instructions img{
  width:50%;
  margin: 0.5vmax;
}

.section-instructions div{
  display: flex;
  justify-content:space-evenly;
  align-items: center;
}

.instruction-rules div img{
  width: 17%;
}







.instruction-power-special img{
  width: 40%;
}

.instruction-power-special span{
  background: transparent;
  font-size: 1.3vmax;
}

.instruction-power-special .table{
  width: 85%;
  display: flex;
  flex-direction: column;
  gap: 0.5vmax;
}

.instruction-power-special .line1, 
.instruction-power-special .line2, 
.instruction-power-special .line3{
  width: 100%;
  background: var(--gradient-green-dark-transp);
  display: flex;
  justify-content: space-between;
  border-radius: 0.2vmax;
  padding: 0.08vmax;
  padding-right: 0.5vmax;
  padding-left: 0.5vmax; 
  color: white;
}

.instruction-power-special .line3{
  background: var(--gradient-red);
}



.instruction-extra-rounds img{
  width: 100%;
}
